<template>
  <el-container class="main-container">
    <!-- 头部区域 -->
    <el-header>
      <!-- 左侧的 logo -->
      <img src="../../assets/images/logo.png" alt="" />
      <!-- 右侧的菜单 -->
      <el-menu
        class="el-menu-top"
        mode="horizontal"
        background-color="#23262E"
        text-color="#fff"
        active-text-color="#409EFF"
      >
        <el-menu-item index="2" @click="logoutFn"
          ><i class="el-icon-switch-button"></i>退出</el-menu-item
        >
      </el-menu>
    </el-header>
    <el-container>
      <!-- 侧边栏区域 -->
      <el-aside width="200px">
        <!-- 左侧边栏的用户信息 -->
        <div class="user-box">
          <img :src="user_pic" alt="" v-if="user_pic" />
          <img src="../../assets/images/defaultLOGO.jpg" v-else />
          <span>欢迎 {{ username }}</span>
        </div>
        <!-- 通过router开启导航路由模式 -->
        <el-menu
          :default-active="$route.fullPath"
          class="el-menu-vertical-demo"
          background-color="#23262E"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          router
        >
          <!-- 首页 -->
          <el-menu-item index="/home">
            <i class="el-icon-s-home"></i>
            <span slot="title">首页</span>
          </el-menu-item>
          <!-- 用户中心 -->
          <el-submenu index="user">
            <template slot="title">
              <i class="el-icon-user"></i>
              <span>用户中心</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/shop-cart">
                <i class="el-icon-shopping-cart-2"></i>
                <span>购物车</span>
              </el-menu-item>
              <el-menu-item index="/order">
                <i class="el-icon-tickets"></i>
                <span>用户订单</span>
              </el-menu-item>
              <!-- <el-menu-item index="/favorites">
                <i class="el-icon-star-off"></i></i>
                <span>菜品搜藏</span>
              </el-menu-item> -->
              <el-menu-item index="/user-info">
                <i class="el-icon-edit"></i>
                <span>基本资料</span>
              </el-menu-item>
              <el-menu-item index="/user-avatar">
                <i class="el-icon-picture-outline-round"></i>
                <span>更换头像</span>
              </el-menu-item>
              <el-menu-item index="/user-pwd">
                <i class="el-icon-key"></i>
                <span>重置密码</span>
              </el-menu-item>
            </el-menu-item-group>
          </el-submenu>

          <!-- v-for渲染左侧店铺列表 -->
          <template v-for="R in shopList">
            <el-submenu :index="String(R.room.rid)" :key="R.room.rid">
              <template slot="title">
                <i class="el-icon-burger"></i>
                <span>{{ R.room.rname }}</span>
              </template>
              <el-menu-item-group>
                <el-menu-item
                  v-for="S in R.shops"
                  :key="S.sid"
                  :index="String('/shop?shopID=' + S.sid)"
                >
                  <i class="el-icon-s-shop"></i>
                  <span>{{ S.sname }}</span>
                </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </template>
        </el-menu>
      </el-aside>
      <!-- 页面主体区域 -->
      <el-container>
        <!-- 二级路由挂载点 -->
        <el-main>
          <keep-alive :include="Home">
            <router-view></router-view>
          </keep-alive>
        </el-main>
        <!-- 底部 footer 区域 -->
        <el-footer>© www.4399.com - 带给你快乐的网站</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
import { shopListAPI } from "@/api/index";
import { mapGetters } from "vuex";
export default {
  name: "my-layout",
  // =============================================以下是Data=============================================

  data() {
    return {
      shopList: [],
    };
  },
  // =============================================以下是Method=============================================
  methods: {
    // 退出登录
    logoutFn() {
      this.$confirm("是否确认退出？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          // 调用mutation方法,清除token串
          this.$store.commit("updateToken", "");
          // 清除userInfo
          this.$store.commit("updateUserInfo", {});
          //   跳转至登录页面
          this.$router.push("/login");
        })
        .catch((err) => err);
    },
    // 获取右侧店铺列表
    async getShopList() {
      const { data: res } = await shopListAPI();
      if (res.code !== 0) return this.$message.error("获取店铺列表失败");
      this.shopList = res.data;
    },
  },
  computed: {
    ...mapGetters(["username", "user_pic"]),
  },
  // =============================================以下是Created=============================================
  created() {},
  // =============================================以下是Mounted=============================================
  mounted() {
    this.getShopList();
  },
};
</script>

<style lang="less" scoped>
.main-container {
  height: 100%;
  .el-header,
  .el-aside {
    background-color: #23262e;
  }
  .el-header {
    padding: 0;
    display: flex;
    justify-content: space-between;
  }
  .el-main {
    overflow-y: scroll;
    height: 0;
    background-color: #f2f2f2;
  }
  .el-footer {
    background-color: #eee;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.avatar {
  border-radius: 50%;
  width: 35px;
  height: 35px;
  background-color: #fff;
  margin-right: 10px;
  object-fit: cover;
}
// 左侧边栏用户信息区域
.user-box {
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  user-select: none;
  img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: #fff;
    margin-right: 15px;
    object-fit: cover;
  }
  span {
    color: white;
    font-size: 12px;
  }
}
// 侧边栏菜单的样式
.el-aside {
  .el-submenu,
  .el-menu-item {
    width: 200px;
    user-select: none;
  }
}
</style>
